<template>
    <div class='gouwuche'>
        <div class="dingdanList">
            <div class="dingdanTitle">
                <div>{{$t('gouwuche[0]')}}</div>
                <div>{{$t('gouwuche[1]')}}</div>
                <div>{{$t('gouwuche[2]')}}</div>
                <div>{{$t('gouwuche[3]')}}</div>
                <div>{{$t('gouwuche[4]')}}</div>
                <div>{{$t('gouwuche[5]')}}</div>
                <div>{{$t('gouwuche[6]')}}</div>
            </div>
            <div v-if="shop_car.length<=0" class="zanwu">
                <img src="../../../assets/img/shangcheng/tubiao.png" alt="">
                <h1>{{$t('gouwuche[7]')}}...</h1>
            </div>
            <div class="list" v-for="(item,i) in shop_car" :key="i">
                <!-- <div class="listTitle">
                    <div>
                        {{item.add_time}}
                    </div>
                    <div>
                        订单号: {{item.order_num}}
                    </div>
                    <div>
                        {{item.status | zhuangtai}}
                    </div>
                </div> -->
                <div class="item">
                    <div>
                        <input type="checkbox" v-model="shop_car[i].checked" :name="item.id" class="inp"/>
                    </div>
                    <div>
                        <div class="img">
                            <img :src="imgUrl + item.thumb" alt="">
                        </div>
                        <div class="text">
                            <h1>{{item.name}}</h1>
                            <p>{{item.intro}}</p>
                        </div>
                    </div>
                    <div>
                        <div></div>
                        <div>¥{{item.price}}</div>
                    </div>
                    <div>
                        <el-input-number v-model="item.num" :min="1" :max="item.stock" :label="$t('gouwuche[8]')" size="mini"></el-input-number>
                    </div>
                    <div>{{item.sku}}</div>
                    <div>¥{{item.price * item.num}}</div>
                    <div>
                        <div @click="shanchu(item.id)">
                            <img src="../../../assets/img/shangcheng/shanchu_02.png" alt="">
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="jiesuan">
                <div>
                    <div @click="quanxuan()">
                        <img src="../../../assets/img/shangcheng/jizhumima_02.png" alt="" v-if="imgShow">
                        <img src="../../../assets/img/shangcheng/jizhumima_01.png" a lt="" v-else>
                    </div>
                    <div @click="quanxuan()">{{$t('gouwuche[9]')}}</div>
                    <div @click="del_car()">{{$t('gouwuche[10]')}}</div>
                </div>
                <div>{{$t('gouwuche[11]')}}{{goodsNum}}{{$t('gouwuche[12]')}}</div>
                <div>
                    <div class="text">{{$t('gouwuche[13]')}}（{{$t('gouwuche[14]')}}）：<span>￥{{zongjiPrice}}</span></div>
                    <div class="btn" @click="jiesuan()" v-if="zongjiPrice<=0">{{$t('gouwuche[15]')}}</div>
                    <div class="btn active" @click="jiesuan()" v-else>{{$t('gouwuche[15]')}}</div>
                </div>
            </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            shop_car:'',//购物车列表
            checked:'',//复选框
            picked:false,//全选
            zongjiPrice:'',//总计
            imgShow:false,//全选img
            timer:'',//定时器
            goodsNum:'',//已选数量
        };
    },
    created:function(){
    },
    filters: {
        zhuangtai:function(value){
            if(value == -1){
                return _this.$t('gouwuche[16]');
            }
            if(value == 1){
                return _this.$t('gouwuche[17]');
            }
            if(value == 2){
                return _this.$t('gouwuche[18]');
            }
            if(value == 3){
                return _this.$t('gouwuche[19]');
            }
        },
    },
    beforeDestroy() {
        if(this.timer) {
            clearInterval(this.timer); //关闭
        }
    },
    mounted:function(){
        var _this = this;
        _this.imgUrl = _this.apis.second;
        _this.geiShop_car()
        _this.timer = window.setInterval(function(){
            var price = 0;
            var num = 0;
            _this.shop_car.forEach(function(e){
                if(e.checked){
                    price += e.price*e.num;
                    num++;
                }else{
                    _this.imgShow = false;
                    _this.picked = false
                }
            })
            _this.zongjiPrice = price
            _this.goodsNum = num
        },500);
    },
    watch:{
    },
    methods:{
        //获取购物车列表
        geiShop_car(){
            var _this = this;
            _this.apis.get(_this.apis.shop_car,{params:{lang:_this.apis.lang}}).then(function(res) {
                if(res.data.code == 0){
                    _this.shop_car = res.data.data
                }
            })
        },
        //删除当前商品
        shanchu(id){
            var _this = this;
            _this.apis.get(_this.apis.del_car,{params:{lang:_this.apis.lang,ids:id}}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.geiShop_car()
                    _this.$message({
                        message: _this.$t('gouwuche[20]'),
                        type: 'success'
                    });
                }
            })
        },
        //全选删除
        del_car(){
            var _this = this;
            var ids = '';
            _this.shop_car.forEach(function(e){
                if(e.checked){
                    ids+=e.id+ ','
                }
            });
            _this.apis.get(_this.apis.del_car,{params:{lang:_this.apis.lang,ids:ids.substring(0, ids.lastIndexOf(','))}}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.$message({
                        type: 'success',
                        message: _this.$t('gouwuche[21]'),
                    });
                    _this.geiShop_car()
                }else{
                    _this.$message({
                        type: 'info',
                        message: res.data.msg,
                    });
                }
            })
        },
        //全选
        quanxuan(){
            var _this = this;
            _this.imgShow = !_this.imgShow
            if(_this.picked){
                 _this.picked = false
                _this.shop_car.forEach(function(e){
                    if(e.status == 0){
                        return;
                    }
                    e.checked = false
                });
                _this.$forceUpdate()
            }else{
                _this.picked = true
                _this.shop_car.forEach(function(e){
                    if(e.status == 0){
                        return;
                    }
                    e.checked = true
                });
                _this.$forceUpdate()
            }
        },  
        //结算
        jiesuan(){
            var _this = this;
            var arr = '';
            _this.shop_car.forEach(function(e){  
                console.log(e)
                if(e.checked){
                    arr+=e.id + '_' + e.num + ','
                }
            });
            _this.apis.get(_this.apis.shopping_cart,{params:{lang:_this.apis.lang,data:arr.substring(0, arr.lastIndexOf(','))}}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.$router.push({path:'querendingdan',query:{title:_this.$t('gouwuche[22]'),id:_this.$route.query.id,num:_this.num,data:res.data.data,type:2}})
                }
            })
        },
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
.gouwuche{
    .dingdanList{
        min-height: 300px;
        max-height: 800px;
        padding-right: 10px;
        overflow-y: scroll;
        position: relative;
        .dingdanTitle{
            display: flex;
            align-items: center;
            text-align: center;
            border-bottom: 2px solid #FFAE02;
            margin-bottom: 20px;
            padding: 0 22px;
            &>div{
                height: 32px;
                line-height: 32px;
            }
            &>div:nth-child(1){
                width: 50px;
            }
            &>div:nth-child(2){
                width: 300px;
            }
            &>div:nth-child(3){
                width: 120px;
            }
            &>div:nth-child(4){
                width: 100px;
            }
            &>div:nth-child(5){
                width: 150px;
            }
            &>div:nth-child(6){
                width: 120px;
            }
            &>div:nth-child(7){
                flex: 1;
            }
        }
        .shanchu{
            line-height: 20px;
            text-align: right;
            span{
                vertical-align: middle;
                cursor:pointer;
            }
            .quanxuan{
                display: inline-block;
                width: 14px;
                height: 14px;
                margin-right: 5px;
                border: 1px solid #C7C7C7;
            }
            .shanchuIcon{
                display: inline-block;
                width: 14px;
                height: 14px;
                margin-right: 5px;
                border: 1px solid #C7C7C7;
            }
        }
        .list{
            background: #FFF2EC;
            margin: 20px 0;
            padding: 14px 0;
            .listTitle{
                display: flex;
                align-items: center;
                padding: 0 22px;
                border-bottom: 1px solid #C7C7C7;
                &>div{
                    height: 33px;
                    line-height: 33px;
                }
                &>div:nth-child(1){
                    line-height: 33px;
                    color: #000000;
                    font-size: 13px;
                    font-weight: bold;
                    margin-right: 15px;
                    span{
                        display: inline-block;
                        width: 14px;
                        height: 14px;
                        margin-right: 10px;
                        background: #fff;
                        border: 1px solid #C7C7C7;
                        vertical-align: middle;
                    }
                }
                &>div:nth-child(2){
                    
                }
                &>div:nth-child(3){
                    flex: 1;
                    text-align: right;
                    font-size: 13px;
                }
            }
            .item{
                display: flex;
                align-items: center;
                text-align: center;
                padding: 10px 22px;
                &>div:nth-child(1){
                    width: 50px;
                    height: 84px;   
                }
                &>div:nth-child(2){
                    width: 300px;
                    display: flex;
                    align-items: top;
                    .img{
                        width: 84px;
                        height: 84px;
                        margin-right: 16px;
                    }
                    .text{
                        flex: 1;
                        h1{
                            font-size: 13px;
                            text-align: left;
                            -webkit-line-clamp:2;
                            -webkit-box-orient: vertical;
                            display: -webkit-box;
                            text-overflow: ellipsis;
                            overflow: hidden;
                        }
                        p{
                            font-size: 12px;
                            color: #959595;
                            -webkit-line-clamp:2;
                            -webkit-box-orient: vertical;
                            display: -webkit-box;
                            text-overflow: ellipsis;
                            overflow: hidden;
                        }
                    }
                }
                &>div:nth-child(3){
                    width: 120px;
                    &>div:nth-child(1){
                        text-decoration:line-through;
                        color: #8F8F8F;
                    }
                    &>div:nth-child(2){
                        font-size: 13px;
                    }
                }
                &>div:nth-child(4){
                    width: 100px;
                    .el-input-number--mini{
                        width: 100px;
                    }
                }
                &>div:nth-child(5){
                    width: 150px;
                    color: #8F8F8F;
                }
                &>div:nth-child(6){
                    width: 120px;
                }
                &>div:nth-child(7){
                    flex: 1;
                    cursor:pointer;
                    div{
                        width: 16px;
                        height: 16px;
                        margin: 0 auto;
                    }
                }
            }
        }
    }
    .jiesuan{
        width: 100%;
        height: 46px;
        margin-right: 20px;
        margin-bottom: 100px;
        background: #e5e5e5;
        display:flex;
        justify-content: space-between;
        align-items: center;
        &>div:nth-child(1){
            width: 200px;
            padding-left: 20px;
            display: flex;
            align-items: center;
            cursor:pointer;
            &>div:nth-child(1){
                margin-right: 4px;
                width: 12px;
                height: 12px;
            }
            &>div:nth-child(2){
                margin-right: 15px;
            }
            &>div:nth-child(3){
                
            }
        }
        &>div:nth-child(2){
            width: 400px;
            text-align: right;
        }
        &>div:nth-child(3){
            flex: 1;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            .text{
                margin-right: 10px;
                color: #3B3B3B;
                font-size: 14px;
                font-weight: bold;
                span{
                    color: #FF6508;
                    font-size: 20px;
                }
            }
            .btn{
                width: 130px;
                height: 46px;
                text-align: center;
                line-height: 46px;
                background: #B0B0B0;
                font-size: 18px;
                color: #FFFFFF;
            }
            .active{
                background: #FFAD01;
            }
        }
    }
}
.zanwu{
    margin: 100px auto;
    text-align: center;
    img{
        width: 338px;
        height: 306px;
    }
    h1{
        color: #b6b6b6;
            font-size: 16px;
            line-height: 50px; 
            margin: 10px 0;
            font-weight: 100;
    }
}
</style>


